.cms {
    .cms-content-fields {
        background: #fbfbfb;
    }

    .ui-tabs-nav li.ui-state-default {
        border-radius: 0;
        border: 0;
        margin: 0px;
        margin-right: 1px;
        a {
            text-shadow: none;
        }
    }
    .ui-tabs-nav li.ui-state-active {
        padding: 0;
        a {
            color: white;
        }
    }
    .cms-content-fields.center.cms-panel-content.cms-panel-padded .SelectionGroup.selectiongroup.parent-mode.nolabel li {
        label {
            margin-bottom: 10px;
            display: block;
        }
    }

    .ui-dialog {
        border: 0 !important;
    }
}
